.dbview .list{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.dbview .list .columns{background-color:#1a1a1a;font-weight:600}.dbview .list .columns .clickable{cursor:pointer}.dbview .list .datarow:nth-child(even){background-color:#050505}.dbview .list span{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:nowrap;flex-wrap:nowrap;-webkit-justify-content:space-between;justify-content:space-between}.dbview .list span .action{font-weight:600;color:#28e9a9;cursor:pointer}.dbview .list span p{padding:5px 0;margin:10px 0;width:150px;text-align:center}.dbview .buttons{margin:4px 0;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.dbview h2,.dbview h1{margin:0}.dbview button.count{width:40px;margin:0 5px}.dbview button.addedit{width:120px}.splitform{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:center;justify-content:center;width:400px}.splitform .partone,.splitform .parttwo{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:0 20px}.normalform{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:200px}.form{background-color:#1a1a1a;padding:20px 20px;border:#383838 1px solid;border-radius:3px;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.form .occupantlist{margin:5px 0;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.form label.lbl{margin:5px 0;font-weight:600}.form input{margin:5px 0}.form input[type="radio"]{margin-right:5px}.form .formbtn{margin:5px 0;-webkit-align-self:flex-end;align-self:flex-end}

.login{background-color:#1f1f1f;min-height:100vh;display:grid;grid-template-columns:1fr 5fr 5fr 1fr;grid-template-rows:1fr 7fr 1fr;grid-template-areas:". . . . " ". form logo . " ". . . . "}.login .loginform{width:37vw;background-color:#121212;border-radius:5px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;grid-area:form}.login .loginform h1{margin:25px 0;letter-spacing:5px}.login .loginform h2{margin:25px 0;font-weight:400}.login .loginform div{width:75%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.login .loginform div input{-webkit-align-self:center;align-self:center;width:93%}.login .loginform div button{-webkit-align-self:center;align-self:center}.login .loginform div input,.login .loginform div button{margin:25px 0}.login .logo{grid-area:logo;justify-self:center;align-self:center}.login .logo img{width:20vw}

